<template>
  <div id="favorite">
    <div class="favoriteBox">
      <div class="favoriteViewBox">
        <div class="imgViewBox">
          <div
            class="imgView"
            v-for="(item, index) in viewList"
            :key="index"
            @click="imgViewClick(item)"
          >
            <img :src="item" alt="" />
          </div>
        </div>
      </div>
      <div class="optionBox"></div>
    </div>
    <div class="cellNextBox" ref="getBox" v-show="cellNextBoxShow"></div>
  </div>
</template>

<script>
import { Execute } from "@/components/xuFunction/execute";
export default {
  name: "Favorite",
  components: {},
  data() {
    return {
      viewList: [],
      index: 0,
      execute: new Execute(),
      cellNextBoxShow: true,
    };
  },

  mounted() {
    this.myFavorite = JSON.parse(localStorage.getItem("myFavorite"));
    this.getView(10);
    window.addEventListener("scroll", this.getBoxGetImg, true);
  },
  beforeUnmount() {
    window.removeEventListener("scroll", this.getBoxGetImg, true);
  },
  unmounted() {
    this.$emit("unmountedF", "");
  },
  methods: {
    getView(num) {
      let index;
      for (let i = this.index; i < num + this.index; i++) {
        if (this.myFavorite[i]) {
          this.viewList.push(this.myFavorite[i]);
        } else {
          index = i;
          break;
        }
        if (num + this.index - 1 == i) {
          index = i;
        }
      }
      this.index = index + 1;
    },
    getBoxGetImg() {
      //进入可视区域内后
      let offset = this.$refs["getBox"].getBoundingClientRect();
      let executeNum = this.execute.differenceComparison(offset.top);
      let offsetTop = offset.top;
      let offsetBottom = offset.bottom;
      // 进入可视区域
      if (offsetTop <= window.innerHeight && offsetBottom >= 0) {
        // 向tableData里的videoUrl直接注入请求地址，就会自动发送请求了
        if (executeNum < 0) {
          if (this.getOddComicSwitch) {
            this.getOddComicSwitch = false;
            this.getView(4);
            if (this.myFavorite.length == this.viewList.length) {
              this.cellNextBoxShow = false;
            }
          }
        }
      } else {
        if (!this.getOddComicSwitch) {
          this.getOddComicSwitch = true;
        }
      }
    },
    imgViewClick(url) {
      let arr = url.split("/");
      let urls = arr[3] + "_" + arr[4] + "_" + arr[5] + "_" + arr[6];
      this.$router.push("/watchcomic/" + urls);
    },
  },
};
</script>

<style scoped>
.favoriteBox {
  display: flex;
}
.favoriteViewBox {
  width: 70%;
  box-sizing: border-box;
  display: flex;
  justify-content: right;
}
.imgViewBox {
  width: 85%;
  box-sizing: border-box;
}
.imgView {
  width: 50%;
  height: 40vw;
  box-sizing: border-box;
  display: inline-block;
  padding: 0 10px;
  overflow: hidden;
  cursor: pointer;
}
.imgView img {
  height: 100%;
}
.optionBox {
  width: 30%;
  box-sizing: border-box;
}
.cellNextBox {
  width: 100%;
  height: 100px;
}
@media screen and (max-width: 1000px) {
  .favoriteViewBox {
    width: calc(100% - 7px);
    margin-left: 7px;
  }
  .optionBox {
    display: none;
  }
  .imgViewBox {
    width: 100%;
  }
  .imgView img {
    height: auto;
    width: 100%;
  }
}
</style>
